@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap");
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  margin: 0;
  color: #5A5C69;
}

p {
  color: #5A5C69;
  font-size: 2rem;
  margin: 0;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Nunito", sans-serif;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

ul {
  margin: 0;
  padding: 0;
  text-decoration: none;
}
ul li {
  margin: 0;
  padding: 0;
  list-style: none;
}
ul li span {
  font-weight: 900;
  color: #FFFFFF;
}

.flex {
  display: flex;
  position: relative;
}
.flex .navbar {
  background-color: #4e73df;
  background-image: linear-gradient(180deg, #4e73df 10%, #224abe 100%);
  background-size: cover;
}
.flex .navbar p {
  color: #FFFFFF;
}

.sidebar-brand {
  display: flex;
  padding: 2.4rem 1.6rem;
  justify-content: center;
  align-items: center;
}
.sidebar-brand .sidebar-brand-icon {
  font-size: 3.5rem;
  color: #FFFFFF;
}
.sidebar-brand .sidebar-brand-text {
  color: #FFFFFF;
  font-weight: 700;
  margin: 0 1.6rem;
}
.sidebar-brand .sidebar-brand-text p {
  font-size: 1.6rem;
}

.siderbar-divider {
  margin: 0 1.6rem 1.6rem;
}

hr {
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  border-bottom: 1px rgba(255, 50, 50, 0);
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

.nav-item {
  color: #FFFFFF;
  font-weight: 700;
  position: relative;
  height: 2rem;
}
.nav-item .nav-link {
  padding: 1.6rem;
  position: relative;
  color: #FFFFFF;
  width: 100;
}

.sidebar-heading {
  color: rgba(255, 255, 255, 0.4);
  padding: 0 1.6rem;
  font-weight: 700;
  font-size: 1rem;
  text-transform: uppercase;
}

.nav {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.contenedor {
  width: 100%;
  flex-direction: column;
  background-color: #F8F9FC;
}
.contenedor .header {
  margin: 0;
  background-color: #FFFFFF;
  box-shadow: 0 0.15rem 1.75rem 0 rgba(58, 59, 69, 0.15);
  height: 7rem;
}

.main {
  height: 100rem;
}